import React, { Component } from 'react'
import { StyleSheet, css } from 'aphrodite'

export default class WorkItem extends Component {
    nl2br(str) {
        const regex = /(\\n)/g
        console.log(str)
        return str.split(regex).map((line, i) => {
            return line.match(regex) ? <br key={i} /> : <li className={css(styles.contentListItem)} key={i}>{line}</li>
        })
    }
    render() {
        const {
            title,
            technology,
            body,
            favorite,
            improvement,
            color,
            image,
            link,
            created_at
        } = this.props
        return (
            <div className={css(styles.item)}>
                {
                    image
                        ?
                        <img src={image} className={css(styles.image)} />
                        :
                        <div className={css(styles.noImage)} >
                            <h3 className={css(styles.noImageText)}>No Image</h3>
                        </div>
                }
                <div className={css(styles.contentContainer)}>
                    <h3 className={css(styles.contentTitle)} style={{ color }}>{title}</h3>
                    <p className={css(styles.createdAt)}>作成日: {created_at}</p>
                    <div style={{ clear: 'left' }} />
                    <p className={css(styles.technologyContainer)}><span style={{color}}>技術</span>: {technology}</p>
                    <ul className={css(styles.contentList)}>
                        {this.nl2br(body)}
                    </ul>
                    <p className={css(styles.subTitle)} style={{color: '#FA776D'}}>お気に入りポイント</p>
                    <ul className={css(styles.contentList)}>
                        {this.nl2br(favorite)}
                    </ul>
                    <p className={css(styles.subTitle)} style={{color: '#F9CDAD'}}>反省点</p>
                    <ul className={css(styles.contentList)}>
                        {this.nl2br(improvement)}
                    </ul>
                    <div className={css(styles.codeBtn)}>
                        <a className={css(styles.codeLink)} href={link}>Githubを見る</a>
                    </div>
                </div>
            </div>
        )
    }
}

const styles = StyleSheet.create({
    item: {
        width: '100%',
        height: '100%',
        borderRadius: 20,
        boxShadow: '2px 2px 10px #eee, -2px -2px 10px #eee',
        marginBottom: 20
    },
    image: {
        width: '100%',
        height: 200,
        borderTopLeftRadius: 20,
        borderTopRightRadius: 20,
        objectFit: 'contain',
        paddingBottom: 3,
        borderBottom: 'solid 1.5px #ccc'
    },
    noImage: {
        display: 'flex',
        width: '100%',
        height: 200,
        justifyContent: 'center',
        alignItems: 'center',
        borderTopLeftRadius: 20,
        borderTopRightRadius: 20,
        borderBottom: 'solid 1.5px #ccc'
    },
    noImageText: {
        fontSize: 25,
        color: '#F9CDAD',
    },
    contentContainer: {
        padding: '20px',
    },
    contentTitle: {
        float: 'left',
        fontSize: 30,
        marginBottom: 10,
    },
    createdAt: {
        float: 'right',
        fontSize: 13,
        color: '#777',
    },
    technologyContainer: {
        fontSize: 14,
        color: '#757575',
        textIndent: '-2.5em',
        wordWrap: 'break-word',
        paddingLeft: 35,
        marginTop: 25,
        marginBottom: 25
    },
    contentList: {
        listStyle: 'none',
        padding: 0,
        paddingLeft: 15,
        marginTop: 10,
        wordWrap: 'break-word',
    },
    contentListItem: {
        fontSize: 16,
        color: '#555',
        textIndent: '-1em'
    },
    subTitle: {
        fontSize: 16,
        fontWeight: 'bold',
        marginTop: 35,
    },
    codeBtn: {
        textAlign: 'center',
        marginTop: 50,
    },
    codeLink: {
        fontSize: 16,
        color: '#757575',
        textDecoration: 'underline solid #757575',
    },
})