import React, { Component, Fragment } from 'react'
import { StyleSheet, css } from 'aphrodite'
import PreparationScreen from '../common/screen/PreparationScreen';
import WorkItem from './lists/items/WorkItem';
import NextScreenButton from '../common/buttons/NextScreenButton';

const List = (props) => {
    const {
        data,
        item
    } = props
    let list = []
    for (let i = 0; i < data.length; i++) {
        list.push(item(data[i], i))
    }
    return (
        <Fragment>
            {list}
        </Fragment>
    )
}
export default class WorkTopScreen extends Component {
    render() {
        const {
            list
        } = this.props
        return (
            <div className={css(styles.container)}>
                <h1 className={css(styles.title)}>Works</h1>
                <div className={css(styles.list)}>
                    <List
                        data={list}
                        item={(item, index) => {
                            return (
                                <WorkItem
                                    key={index}
                                    {...item}
                                />
                            )
                        }}
                    />
                </div>
                <NextScreenButton
                    uri='/blogs'
                    text='ブログを見る'
                />
                {/* <PreparationScreen /> */}
            </div>
        )
    }
}

const styles = StyleSheet.create({
    container: {
        width: '100%',
        height: '100%',
        backgroundColor: '#fff',
        paddingTop: 80,
        paddingBottom: 30
    },
    title: {
        textAlign: 'center',
        color: '#FA776D',
        fontSize: 30,
        marginBottom: 50,
    },
    list: {
        width: '90%',
        height: '100%',
        padding: '20px 0',
        maxWidth: 480,
        margin: '0 auto',
    },
})